
import {getAllInfo, getSubMenu, getSearch} from '@/api/address-book';
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'addressBook',
components: {Scroller, Cell, Group},
data() {
return {
firstList: true,//首页
subList: false,//子部门或员工页显示判断
people: true,//员工列表显示判断
isChecked: false,
showContent: true,//展示内容判断
isSmall: false,//搜索框长短
cancle: false,//取消
searchText: "",//搜索关键词
branchList: [],//头部导航列表
allInfoList: "",//首页信息
subInfoList: "",//子部门信息
peopleList: "",//员工信息
choseId: "",//部门id
isSelf: false,//是否本目录
selfName: "",//本目录名字,
nullTips: false,//无员工提示
showResult: false,//显示搜索结果判断
searchResult: "",
}
},
methods: {
searchOne(){//搜索页面
this.showContent = false;
this.isSmall = true;
this.cancle = true;
this.getSearchList();
this.showResult = true;
},
cancleQuit(){//取消搜索
this.showContent = true;
this.isSmall = false;
this.cancle = false;
this.showResult = false;
},
goPrevious(item, index){//头部导航
console.log(item);
if (item.id === 1) {
this.firstList = true;
this.subList = false;
this.getAllList();
this.branchList.splice(-1, this.branchList.length - index - 1);
} else {
this.subList = true;
this.getSubList(item.id || item.departmentId);
this.branchList.splice(-1, this.branchList.length - index - 1);
}
this.isSelf = true;
},
itemClick(item){
this.branchList.push(item);
this.selfName = item.name;
this.isChecked = true;
this.choseId = item.id;
this.firstList = false;
this.subList = true;
this.getSubList(this.choseId);
},
goDetail(item){
this.$router.push({
name: 'memberDetail',
params: {"id": item.userId}
});
},
getAllList(){//获取首页列表信息
getAllInfo({},).then(response => {
if (response.data && response.data.rows) {
this.allInfoList = response.data.rows[0].deptInfo;
if (this.branchList.length === 0) {
this.branchList.push({
id: response.data.rows[0].deptInfo[0].id,
name: response.data.rows[0].deptInfo[0].name
});//公司抬头
}
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
getSubList(param){//获取子部门或部门员工列表信息
getSubMenu({id: param},).then(response => {
if (response.data && response.data.rows) {
this.subInfoList = response.data.rows[0].childrenDept;
if (response.data.rows[0].childrenDept === null) {
this.firstList = false;
this.subList = false;
this.people = false;
this.nullTips = true;
}
this.peopleList = response.data.rows[0].deptStaff;
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
getSearchList(){
getSearch({"key": this.searchText, "page": "1", "pagesize": "15"},).then(response => {
if (response.data && response.data.rows) {
this.searchResult = response.data.rows;
console.log("搜索", this.searchResult);
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
});
},
},
mounted() {
this.getAllList();
this.getSearchList();
},
created() {
},
}



